* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  height: 100vh;
  background: linear-gradient(-45deg, #f3d007, rgba(10, 28, 192, .863), #f3d007, rgba(10, 28, 192, .863)) 0 0 / 400% 400%;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

img {
  max-width: 100%;
  border-radius: 5px;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.header-image {
  width: 100%;
  text-align: center;
  z-index: 33;
  background: linear-gradient(-45deg, #f3d007, rgba(10, 28, 192, .863), #f3d007, rgba(10, 28, 192, .863)) 0 0 / 400% 400%;
  padding-bottom: 10px;
  animation: gradient 15s infinite;
  position: fixed;
}

.search-form {
  justify-content: center;
  gap: 20px;
  display: flex;
}

input, .btn {
  text-align: center;
  border: none;
  border-radius: 12px;
  padding-top: 5px;
  padding-bottom: 5px;
  box-shadow: 1px 4px 10px rgba(111, 15, 115, .2), 5px 6px 10px rgba(20, 1, 135, .85);
}

.btn {
  color: #fff;
  background-color: #e49778;
}

input:hover, input:focus {
  box-shadow: 1px 4px 10px rgba(111, 15, 115, .2), 0 2px 10px 5px rgba(20, 1, 135, .85);
}

.btn:hover {
  background-color: red;
  border-color: #212121;
  box-shadow: 1px 4px 10px rgba(111, 15, 115, .2), 0 2px 10px 5px rgba(20, 1, 135, .85);
}

.section-gallery {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.gallery-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-bottom: 40px;
  display: flex;
}

.gallery__item {
  height: 200px;
  border-radius: 5px;
  position: relative;
  overflow: hidden;
  box-shadow: 4px 6px 16px rgba(111, 15, 115, .2), 5px 6px 11px rgba(20, 1, 135, .85);
}

.info {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.thumb {
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
}

.gallery__image {
  object-fit: contain;
  height: 100%;
  width: 100%;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.sl-image {
  border-radius: 5px;
  box-shadow: 4px 6px 16px rgba(111, 15, 115, .2), 5px 6px 11px rgba(20, 1, 135, .85);
}

.sl-caption {
  text-align: center;
  background-color: #b6b7e4;
  border-radius: 5px;
  box-shadow: 4px -5px 16px rgba(111, 15, 115, .2), 5px -5px 11px rgba(20, 1, 135, .85);
}

.photo-card {
  width: 300px;
  min-height: 300px;
  background-color: #f0f8ff;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
  overflow: hidden;
  box-shadow: 4px 6px 16px rgba(111, 15, 115, .2), 5px 6px 11px rgba(20, 1, 135, .85);
}

